import React, { useContext } from 'react';
import { Link } from 'react-router-dom';

import LanguageContext from  '../../config/context/languageContext';
import Button from '../../Components/Button/Button';

import './About.scss';
import profilePicture from '../../assets/images/profile.jpg';

const About = () => {
    const language = useContext(LanguageContext);

    const content = {
        pt: {
            title: 'Sobre Mim',
            description: [
                `Sou um desenvolvedor web fullstack que constrói sites, sistemas e aplicações web e mobile, desde a criação das interfaces, servidores e banco de dados, além da integração entre ambas as partes.`,
                `Me especializo em Javascript e busco estar atualizado com as tecnologias mais modernas para oferecer os melhores serviços possíveis, com foco em produzir uma grande experiência de usuário e criar software de qualidade e de fácil manutenção seguindo boas práticas.`,
                `Além disso tenho bastante interesse pela área de design e acompanho ativamente as tendências de design de interfaces e de experiência de usuário.`,
                `No momento atuando como freelancer e aberto para ofertas de trabalho.`
            ],
            seeMyWork: 'Ver Projetos',
            contactMe: 'Contato'
        },
        en: {
            title: 'About Me',
            description: [
                `I am a fullstack web developer, based in Brazil, who builds websites, systems as well as web and mobile applications, since the creation of interfaces, servers and databases, and integration between both parties.`,
                `I specialize in Javascript and seek to be updated with the most modern technologies to offer the best possible services, with a focus on producing a great user experience and creating quality and easy-to-maintain software following good practices.`,
                `In addition, I am very interested in design and actively follow trends in UI & UX design.`,
                `Currently working as a freelancer and open to job offers.`
            ],
            seeMyWork: 'See my Work',
            contactMe: 'Contact Me'
        }
    }

    return (
        <div className="about__container">
            <h1 className="about__title">
                {content[language].title}
            </h1>
            <div className="about__profile-picture">
                <img src={profilePicture} alt="Profile"/>
            </div>
            <div className="about__description">
                {content[language].description.map((p, i) => <p key={i}>{p}</p>)}
            </div>
            <div className="about__buttons">
                    <Link to="/my-work">
                        <Button 
                            text={content[language].seeMyWork}
                        />
                    </Link>
                    <Link to="/contact">
                        <Button
                            text={content[language].contactMe}
                        />
                    </Link>
                </div>
        </div>
    );
}


export default About;